.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.btn {
  width:90%;
  margin:0 auto;
  background: #d16b6b;
  background-image: -webkit-linear-gradient(top, #d16b6b, #a34b4b);
  background-image: -o-linear-gradient(top, #d16b6b, #a34b4b);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#d16b6b), to(#a34b4b));
  background-image: linear-gradient(to bottom, #d16b6b, #a34b4b);
  font-family: Arial;
  color: #ffffff;
  font-size: 95%;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
}

.btn:hover {
  background: #ffd9d9;
  background-image: -webkit-linear-gradient(top, #ffd9d9, #b58080);
  background-image: -o-linear-gradient(top, #ffd9d9, #b58080);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#ffd9d9), to(#b58080));
  background-image: linear-gradient(to bottom, #ffd9d9, #b58080);
  text-decoration: none;
}
body{
  -webkit-user-select: none; /* Chrome/Safari */
-moz-user-select: none; /* Firefox */
-ms-user-select: none; /* IE10+ */

/* Rules below not implemented in browsers yet */
-o-user-select: none;
user-select: none;
}

/*#grid{
  width: 40%;
}*/
.gridStyle {
  display:-ms-flexbox;
  display:flex;
  background: #FAFAFA;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;

}
.rowStyle {
  display:contents;
}
.btnContainer{
  width:30%;
  margin:0 auto;
  display:-ms-flexbox;
  display:flex;
}
.inforContainer{
  width:30%;
  height:35px;
  margin:0 auto;
  display: -ms-flexbox;
  display: flex;

  -ms-flex-align: center;

  align-items: center;
}
.inforContainer *{
  width:50%;
}



